'use client';

import React, { useEffect, useState } from 'react';
import { Dumbbell, Phone } from 'lucide-react';

export default function MainNav() {
  const [isScrolled, setIsScrolled] = useState(false);
  const [active, setActive] = useState<string>('inicio');
  const sections = ['inicio', 'graficos', 'preco', 'pagamento', 'contato'];

  useEffect(() => {
    const onScroll = () => {
      setIsScrolled(window.scrollY > 8);
      const offsets = sections.map((id) => {
        const el = document.getElementById(id);
        if (!el) return { id, top: Number.POSITIVE_INFINITY };
        const rect = el.getBoundingClientRect();
        return { id, top: Math.abs(rect.top) };
      });
      offsets.sort((a, b) => a.top - b.top);
      setActive(offsets[0].id);
    };
    window.addEventListener('scroll', onScroll);
    window.addEventListener('resize', onScroll);
    onScroll();
    return () => {
      window.removeEventListener('scroll', onScroll);
      window.removeEventListener('resize', onScroll);
    };
  }, []);

  const scrollToId = (id: string) => {
    const el = document.getElementById(id);
    if (el) el.scrollIntoView({ behavior: 'smooth' });
  };

  const openSignup = () => {
    try { localStorage.setItem('openSignupOnLoad', '1'); } catch {}
    if (typeof window !== 'undefined') {
      window.location.hash = 'open-signup';
      (window as any).openSignupModal?.();
    }
    scrollToId('pagamento');
  };

  return (
    <header className={`fixed top-0 inset-x-0 z-50 border-b border-gray-200 ${isScrolled ? 'bg-white/90 backdrop-blur-md' : 'bg-white/80 backdrop-blur-sm'} transition-colors`}>
      <div className="max-w-6xl mx-auto px-4">
        <div className="flex items-center justify-between h-14">
          {/* Brand */}
          <button className="flex items-center gap-3 group" onClick={() => scrollToId('inicio')} aria-label="Ir ao in√≠cio">
            <div className="relative">
              <div className="w-10 h-10 gradient-bg-primary rounded-xl flex items-center justify-center transform transition-all duration-300 group-hover:scale-110">
                <Dumbbell className="w-5 h-5 text-white" />
              </div>
              <div className="absolute inset-0 w-10 h-10 gradient-bg-primary rounded-xl opacity-30 blur-lg"></div>
            </div>
            <div className="hidden sm:block text-left">
              <div className="font-orbitron text-xl font-bold text-gray-900 leading-tight">RICARDO <span className="gradient-text">TEAM</span></div>
              <div className="text-[10px] text-gray-600 tracking-wider">PERSONAL TRAINER</div>
            </div>
          </button>

          {/* Always-visible horizontal nav */}
          <nav className="flex items-center gap-6 md:gap-8 overflow-x-auto whitespace-nowrap">
            {sections.map((id) => (
              <button
                key={id}
                onClick={() => scrollToId(id)}
                className={`${active === id ? 'nav-link nav-link-active' : 'nav-link'}`}
                aria-current={active === id ? 'page' : undefined}
              >
                {id === 'inicio' ? 'In√≠cio' : id === 'graficos' ? 'Gr√°ficos' : id === 'preco' ? 'Pre√ßo' : id === 'pagamento' ? 'Pagamento' : 'Contato'}
              </button>
            ))}
            <button onClick={openSignup} className="ml-2 btn-primary btn-sm">Contratar Agora</button>
            <button onClick={() => scrollToId('contato')} className="ml-1 btn-outline btn-sm flex items-center gap-2">
              <Phone className="w-4 h-4" /> Contato
            </button>
          </nav>
        </div>
      </div>
    </header>
  );
}