'use client';

import React, { useState, useEffect } from 'react';
import { ArrowRight, Phone } from 'lucide-react';
import Modal from './Modal';

interface FormData {
  name: string;
  email: string;
  cpf: string;
  phone: string;
}

export default function PaymentInfinityPay() {
  const [data, setData] = useState<FormData>({ name: '', email: '', cpf: '', phone: '' });
  const [agree, setAgree] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const redirectToCheckout = () => {
    const checkout = process.env.NEXT_PUBLIC_INFINITYPAY_CHECKOUT_URL;
    if (!checkout) {
      alert('Configure a vari√°vel NEXT_PUBLIC_INFINITYPAY_CHECKOUT_URL para habilitar o checkout do Infinity Pay.');
      return;
    }
    const params = new URLSearchParams({
      name: data.name,
      email: data.email,
      cpf: data.cpf,
      phone: data.phone,
      amount: '14999', // em centavos
      description: 'Plano de Treino Ricardo Team',
    });
    window.location.href = `${checkout}?${params.toString()}`;
  };

  const openWhatsApp = () => {
    const msg = 'Ol√° Ricardo! Acabei de realizar o pagamento do plano e estou enviando o comprovante.';
    const url = `https://wa.me/5511999999999?text=${encodeURIComponent(msg)}`;
    window.open(url, '_blank');
  };

  const canSubmit = () => {
    return data.name && data.email && data.cpf && data.phone && agree;
  };

  // Fun√ß√£o global para abrir o modal a partir de outros componentes + fallback
  useEffect(() => {
    (window as any).openSignupModal = () => setIsModalOpen(true);

    // Fallback: abre automaticamente se houver flag persistida ou hash na URL
    try {
      const shouldOpen = localStorage.getItem('openSignupOnLoad') === '1' || window.location.hash === '#open-signup';
      if (shouldOpen) {
        setIsModalOpen(true);
        localStorage.removeItem('openSignupOnLoad');
        // limpar o hash sem recarregar a p√°gina
        if (window.history && window.location.hash) {
          window.history.replaceState(null, '', window.location.pathname + window.location.search);
        }
      }
    } catch {}

    return () => { delete (window as any).openSignupModal; };
  }, []);

  return (
    <section id="pagamento" className="py-20 gradient-bg-dark relative overflow-hidden">
      {/* Background Elements */}
      <div className="absolute inset-0 opacity-5">
        <div className="absolute top-20 left-10 w-40 h-40 rounded-full bg-white/20"></div>
        <div className="absolute bottom-20 right-10 w-32 h-32 rounded-full bg-white/15"></div>
      </div>

      <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8 relative z-10">
        <div className="text-center mb-12">
          <h2 className="text-4xl md:text-5xl font-bold text-white mb-6 font-orbitron">
            Finalizar <span className="text-yellow-300">Pagamento</span>
          </h2>
          <p className="text-xl text-white/90 max-w-2xl mx-auto">
            Clique em contratar para abrir o formul√°rio seguro e concluir o pagamento de R$ 149,99
          </p>
        </div>

        <div className="card-modern p-10 relative overflow-hidden">
          {/* Card decoration */}
          <div className="absolute top-0 right-0 w-24 h-24 gradient-bg-primary opacity-10 rounded-full -translate-y-12 translate-x-12"></div>
          
          {/* CTA abre o modal */}
          <div className="text-center space-y-6">
            <div className="gradient-bg-primary p-6 rounded-2xl text-white">
              <div className="flex items-center justify-center gap-4 mb-2">
                <span className="text-2xl">üí∞</span>
                <span className="text-lg font-semibold">Valor Total:</span>
                <span className="text-3xl font-bold text-yellow-300">R$ 149,99</span>
              </div>
              <p className="text-white/90 text-sm">Pagamento √∫nico ‚Ä¢ Sem mensalidades ‚Ä¢ Acesso completo</p>
            </div>

            <button
              onClick={() => setIsModalOpen(true)}
              className="w-full btn-primary text-xl py-5 shadow-2xl hover:shadow-3xl transform hover:scale-105 transition-all duration-300 flex items-center justify-center gap-2"
            >
              üöÄ Contratar Agora
              <ArrowRight className="w-5 h-5" />
            </button>

            <div className="flex items-center justify-center gap-4 text-sm text-gray-300">
              <span>üîí Pagamento Seguro</span>
              <span>‚Ä¢</span>
              <span>‚úÖ Dados Protegidos</span>
              <span>‚Ä¢</span>
              <span>‚ö° Processamento R√°pido</span>
            </div>
          </div>
        </div>

        {/* Ap√≥s pagamento */}
        <div className="mt-10 card-modern p-8 text-center">
          <h4 className="text-2xl font-bold text-gray-900 mb-4">üì± Ap√≥s o Pagamento</h4>
          <p className="text-gray-700 mb-6 text-lg">
            Envie o comprovante via WhatsApp para o Ricardo. Seu treino personalizado come√ßar√° imediatamente ap√≥s a confirma√ß√£o!
          </p>
          <button onClick={openWhatsApp} className="btn-outline flex items-center gap-3 mx-auto text-lg px-8 py-4">
            <Phone className="w-5 h-5" />
            Abrir WhatsApp do Ricardo
          </button>
          <p className="text-gray-500 text-sm mt-4">
            Resposta em at√© 2 horas ‚Ä¢ Suporte completo inclu√≠do
          </p>
        </div>
      </div>

      {/* Modal com formul√°rio */}
      <Modal open={isModalOpen} onClose={() => setIsModalOpen(false)} title="Cadastro e Pagamento">
        <form onSubmit={(e) => { e.preventDefault(); redirectToCheckout(); }} className="space-y-6">
          <div className="grid md:grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-3">üë§ Nome Completo</label>
              <input
                name="name"
                value={data.name}
                onChange={onChange}
                className="w-full px-4 py-4 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 transition-all duration-300"
                placeholder="Seu nome completo"
              />
            </div>
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-3">üìß E-mail</label>
              <input
                name="email"
                type="email"
                value={data.email}
                onChange={onChange}
                className="w-full px-4 py-4 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 transition-all duration-300"
                placeholder="seu@email.com"
              />
            </div>
          </div>

          <div className="grid md:grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-3">üÜî CPF</label>
              <input
                name="cpf"
                value={data.cpf}
                onChange={onChange}
                className="w-full px-4 py-4 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 transition-all duration-300"
                placeholder="000.000.000-00"
              />
            </div>
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-3">üì± Telefone/WhatsApp</label>
              <input
                name="phone"
                value={data.phone}
                onChange={onChange}
                className="w-full px-4 py-4 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 transition-all duration-300"
                placeholder="(11) 99999-9999"
              />
            </div>
          </div>

          <label className="flex items-center gap-3 text-gray-700 bg-gray-50 p-4 rounded-xl">
            <input type="checkbox" checked={agree} onChange={(e) => setAgree(e.target.checked)} className="w-5 h-5" />
            <span className="text-sm">Concordo em enviar o comprovante de pagamento via WhatsApp ap√≥s concluir.</span>
          </label>

          <button
            type="submit"
            disabled={!canSubmit()}
            className="w-full btn-primary text-xl py-5 shadow-2xl hover:shadow-3xl transform hover:scale-105 transition-all duration-300 flex items-center justify-center gap-2"
          >
            üí≥ Pagar R$ 149,99 - Infinity Pay
            <ArrowRight className="w-5 h-5" />
          </button>
        </form>
      </Modal>
    </section>
  );
}